<template>
	<view style="margin: 10rpx;">
		<!-- 基本信息与发表内容的显示 -->
		<view>
			<view class="head_title">{{communityinfo.title}}</view>
			<view class="head_mid">
				<image class="head_img" :src="communityinfo.headSrc"></image>
				<view class="head_name">{{communityinfo.name}}</view>
			</view>
			<view class="heda_time">发表时间：{{communityinfo.time | dateStr('YYYY年MM月DD日')}}</view>
			<view class="head_text">
			<text selectable>
				{{communityinfo.content}}
			</text>
			</view>
		</view>
		
		<!-- 评论模块 -->
		<view >
			<view class="but" @click="comment">评论</view>
		</view>
		<view v-show="isShow">
			<!-- 评论区 -->
			<view class="text_main">
				<view>
				<textarea rows="3" class="text1" 
				adjust-position @confirm="send" confirm-type="done"
				v-model="textValue" placeholder="请输入140字以内的评论内容..." />
				</view>
				<view style="margin-top: 20rpx;">
					<button class="text_but" type="default" @click="send">发表评论</button>
				</view>
			</view>
			
			<!-- 评论信息显示 -->
			<view class="text_main" v-for="(comment,index) in comments" :key="index">
				<view style="color: #007AFF;">{{comment.name}} 评论：</view>
				<p style="text-indent: 2em;">{{comment.content}}</p>
				<view style="text-align: right;">{{comment.time | dateStr('YYYY年MM月DD日')}}</view>
			</view>
		</view>
		<view class="kongbai"></view>
		
		<!-- 图片模块 -->
		<view class="buttom">
			<view class="buttom_img" @click="display = !display" >图片展示</view>
			<view v-show="display" v-for="(img, index) in communityinfo.imgs" :key="index">
				<image lazy-load="true" class="buttom_img2" mode="aspectFill" :src="img.src"></image>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				isShow: false,
				display: false,
				textValue: '',
				commentSend: {},
				comments:[
					{
						id: '1',
						name: '张三',
						time: '2020-12-12',
						content: `猫，属于猫科动物，分家猫、野猫，是全世界家庭中较为广泛的宠物。
						家猫的祖先据推测是起源于古埃及的沙漠猫，
						波斯的波斯猫，已经被人类驯化了3500年（但未像狗一样完全地被驯化）`,
						infoId: '1'
					}
				],
				id: '',
				communityinfo: {
					id:'1',
					title: '这只小猫好可爱',
					name: '李四',
					headSrc: '../../static/img/11.jpg',
					time: '2020-12-12',
					content: `猫的趾底有脂肪质肉垫，因而行走无声，捕鼠时不会惊跑鼠，趾端生有锐利的指甲。
				爪能够缩进和伸出。猫在休息和行走时爪缩进去，只在捕鼠和攀爬时伸出来，防止指甲被磨钝。
				猫的前肢有五指，后肢有四指。猫的牙齿分为门齿、犬齿和臼齿。
				犬齿特别发达，尖锐如锥，适于咬死捕到的鼠类，臼齿的咀嚼面有尖锐的突起，适于把肉嚼碎；门齿不发达。`,
					imgs:[
						{src:'../../static/img/5.jpg'},
						{src:'../../static/img/6.jpg'},
						{src:'../../static/img/7.jpg'}
					]
				},
				nickName: ''
			}
		},
		onLoad(option) {
			if(option !== null) {
				this.id = option.id
			}
			//this.getImages();
			uni.getStorage({
			    key: 'wxUserInfo',
			    success: (res) => {
			        //console.log(res.data);
					this.nickName =res.data.nickName
			    }
			});
		},
		onPullDownRefresh(){
			/*
			this.communityinfo.id = ''
			this.communityinfo.title = ''
			this.communityinfo.name = ''
			this.communityinfo.headSrc = ''
			this.communityinfo.time = ''
			this.communityinfo.content = ''
			this.communityinfo.imgs = []
			*/
			uni.showLoading({ title: '正在刷新', mask: true });
			//this.getImages();
			uni.hideLoading();
			uni.stopPullDownRefresh();	
		},
		onReachBottom(){
			uni.showToast({
				title:'没有数据了',
				duration: 1000,
				icon:"none"
			})
		},
		onBackPress(e) {
			uni.switchTab({
				url: './home'
			});
			return true;
		},
		methods:{
			comment(){
				if(!this.isShow){
					//this.getcomments();
				}
				this.isShow = !this.isShow
			},
			send(){
				if(this.nickName !== ''){
					this.commentSend.name = this.nickName
					this.commentSend.time = Date.now()
					this.commentSend.content = this.textValue
					this.commentSend.infoId = this.id
					this.textValue = ''
					this.comments =this.comments.concat(this.commentSend)
					uni.showToast({title:'发表成功',duration: 1000})
					//console.log(this.comments)
					
					/*
					this.request({
						url: this.$Url+'/home/communityinfocommont',
						data: this.commentSend,
						method: "POST"
					}).then(result=>{
						this.textValue = ''
						this.comments =this.comments.concat(this.commentSend)
						uni.showToast({title:'发表成功',duration: 1000})
						if(result){
							
						}
					}).catch(err =>{
						uni.showToast({title: '获取数据失败',duration: 2000});
					})
					*/
				}
				else{
					uni.showModal({
					    title: '提示',
					    content: '您还未登录，前往登录！',
					    success: function (res) {
					        if (res.confirm) {
								uni.switchTab({
								    url: '/pages/me/me'
								});
					        } else if (res.cancel) {
					   //          console.log('用户点击取消');
					        }
					    }
					});
				}
				
			},
			getcomments(){
				this.request({
					url: this.$Url+'/home/communityinfocommont',
					data: this.id,
					method: "POST"
				}).then(result=>{
					if(result.data.length>1){
						this.comments = result.data
					}else{
						uni.showToast({
						    title: '获取数据失败',
						    duration: 2000
						});
					}
					
				}).catch(err =>{
					uni.showToast({
					    title: '获取数据失败',
					    duration: 2000
					});
				})
			},
			getImages(){
				if(this.id !== ''){
				this.request({
					url: this.$Url+'/home/communityinfo',
					data: this.id,
					method: "POST"
				}).then(result=>{
					if(result.data.length>1){
						this.communityinfo = result.data
					}else{
						uni.showToast({
						    title: '获取数据失败',
						    duration: 2000
						});
					}
					
				}).catch(err =>{
					uni.showToast({
					    title: '获取数据失败',
					    duration: 2000
					});
				})
				}
			}
		}
	}
</script>

<style scoped>
	.head_title{
		display: flex;
		padding: 20rpx;
		font-size: 50rpx;
		color: #FFA500;
		border-bottom: 1rpx solid #ccc;
	}
	.head_mid{
		display: flex;
		flex-wrap: nowrap;
	}
	.head_img{
		width: 70rpx;
		height: 70rpx;
		border-radius: 35rpx;
		margin: 10rpx;
	}
	.head_name{
		font-size: 50rpx;
		padding: 13rpx;
	}
	.heda_time{
		display: flex;
		flex-direction: row-reverse;
		padding: 20rpx;
	}
	.head_tex{
		display: flex;
		justify-content: center;
		margin: 20rpx;
		color: #808080;
	}
	
	.but{
		display: flex;
		justify-content: center;
		padding-top: 4rpx;
		margin: 20rpx;
		width: 700rpx;
		height: 60rpx;
		border-radius: 10px;
		border: 2rpx solid #EE1289;
		background-color: #fff;
	}
	.text_main{
		margin: 20rpx;
		
	}
	.text1{
		border: 1rpx solid #8A2BE2;
		width: 700rpx;
		border-radius: 10rpx;
	}
	.text_but{
		width: 700rpx;
		height: 80rpx;
		font-size: 35rpx;
		background-color: #00BFFF;
		color: #fff;
	}
	
	.buttom{
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	.kongbai{
		width: 700rpx;
		height: 20rpx;
	}
	.buttom_img{
		display: flex;
		margin: 20rpx;
		border-radius: 10px;
		justify-content: center;
		border: 2rpx solid #EE1289;
		width: 700rpx;
		height: 60rpx;
		padding-top: 4rpx;
	}
	.buttom_img2{
		display: flex;
		padding: 30rpx;
		justify-content: center;
		width: 700rpx;
	}
</style>
